<template>

   <div>

        <swiper :options="swiperOption2">
            <swiper-slide>Slide 1</swiper-slide>
            <swiper-slide>Slide 2</swiper-slide>
            <swiper-slide>Slide 3</swiper-slide>
            <swiper-slide>Slide 4</swiper-slide>
            <swiper-slide>Slide 5</swiper-slide>
            <swiper-slide>Slide 6</swiper-slide>
            <swiper-slide>Slide 7</swiper-slide>
            <swiper-slide>Slide 8</swiper-slide>
            <swiper-slide>Slide 9</swiper-slide>
            <swiper-slide>Slide 10</swiper-slide>
        </swiper>


       <div class="weui-panel weui-panel_access">
            <div class="weui-panel__hd">图文组合列表</div>
            <div class="weui-panel__bd">
                <swiper :options="swiperOption" ref="swiper">
                <swiper-slide class="content">
                    <a href="javascript:void(0);" class="weui-media-box weui-media-box_appmsg">
                        <div class="weui-media-box__hd">
                            <img class="weui-media-box__thumb" src="data:image/png;base64," alt="">
                        </div>
                        <div class="weui-media-box__bd">
                            <h4 class="weui-media-box__title">标题一</h4>
                            <p class="weui-media-box__desc">由各种物质组成的巨型球状天体，叫做星球。星球有一定的形状，有自己的运行轨道。</p>
                        </div>
                    </a>
                </swiper-slide>
                
                <swiper-slide class="menu">
                   <div class="weui-grids">
                        <a href="javascript:;" class="weui-grid">
                            <div class="weui-grid__icon">
                                <!-- <img src="./images/icon_tabbar.png" alt=""> -->
                            </div>
                            <p class="weui-grid__label">积极</p>
                        </a>
                        <a href="javascript:;" class="weui-grid">
                            <div class="weui-grid__icon">
                                <!-- <img src="./images/icon_tabbar.png" alt=""> -->
                            </div>
                            <p class="weui-grid__label">消极</p>
                        </a>
                        <a href="javascript:;" class="weui-grid">
                            <div class="weui-grid__icon">
                               <!-- <img src="./images/icon_tabbar.png" alt=""> -->
                            </div>
                            <p class="weui-grid__label">查看更多</p>
                        </a>
                   </div>
                </swiper-slide>
                </swiper> 
                <a href="javascript:void(0);" class="weui-media-box weui-media-box_appmsg">
                    <div class="weui-media-box__hd">
                        <img class="weui-media-box__thumb" src="data:image/png;base64," alt="">
                    </div>
                    <div class="weui-media-box__bd">
                        <h4 class="weui-media-box__title">标题二</h4>
                        <p class="weui-media-box__desc">由各种物质组成的巨型球状天体，叫做星球。星球有一定的形状，有自己的运行轨道。</p>
                    </div>
                </a>
            </div>
            <div class="weui-panel__ft">
                <a href="javascript:void(0);" class="weui-cell weui-cell_access weui-cell_link">
                    <div class="weui-cell__bd">查看更多</div>
                    <span class="weui-cell__ft"></span>
                </a>    
            </div>
        </div>

       <!-- swiper
      <swiper :options="swiperOption" ref="swiper">
        <swiper-slide class="menu">Menu slide</swiper-slide>
        <swiper-slide class="content">
          <div class="menu-button" :class="{ 'cross': menuCross }" @click="toggleMenu">
            <div class="bar"></div>
            <div class="bar"></div>
            <div class="bar"></div>
          </div>
          Content slide
        </swiper-slide>
      </swiper> 
      -->
   </div>
</template>

<script>

  export default {
    name: 'swiper-test',
     data() {
      let self = this
      return {
        menuCross: true,
        swiperOption: {
          slidesPerView: 'auto',
          initialSlide: 0,
          resistanceRatio: .00000000000001,
          slideToClickedSlide: true
        },
        swiperOption2: {
          pagination: '.swiper-pagination',
          slidesPerView: 6,
          initialSlide: 6,
          paginationClickable: true,
          spaceBetween: 0
        }
        
      }
    },
    computed: {
      swiper() {
        return this.$refs.swiper.swiper
      }
    },
    methods: {
      toggleMenu() {
        if (this.swiper.previousIndex == 0) this.swiper.slidePrev()
      }
    }
  }
</script>

<style scoped>
  .menu {
    min-width: 100px;
    width: 70%;
    max-width: 320px;
  }
  .content {
    width: 100%;
  }
</style>


